.header{
    display: flex;
    background-color: #444;
    justify-content: space-between;
    align-items: center;
    z-index: 100000;
    width: 100%;
    height: 70px;
    transition: 0.4s;
    font-size:20px;
    box-shadow: 0px 5px 5px black;
  }
  
  .navbar{
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 10px;
    width: 80%;
    margin: auto;
  }
  
  .navlist{
    display: flex;
    align-items: center;
    justify-content: center;
    list-style-type: none;
    padding: 0px;
    margin: 0px;
    gap: 10px;
  
  }
  
  .navitem {
  
    display: block;
    text-decoration: none;
    color: white;
    padding: 10px;
    transition: color 0.2s;
  }
  
  .navitem:hover{
    color: #888;
  }


.hamburger{
    display:none;
}

.bar{
    display: block;
    width: 25px;
    height: 3px;
    margin: 5px auto;
    margin-right: 5px;
    -webkit-transition: all 0.3s ease-in-out;
    transition: all 0.3s ease-in-out;
    background-color: white;

}

@media (max-width: 767px) {
    .navlist {
        display: none;
        position: relative;
        right: 0;
        top: 90px;
        gap:15;
        flex-direction: column;
        background-color: #333;
        width: 70%;
        text-align: center;
        transition: all 0.3s;
        margin-top: -20px;
        height: 100%;
  
    }

    .show {
      display: flex;
      position: fixed;
      right: 0;
      top: 90px;
      gap:15;
      flex-direction: column;
      background-color: #333;
      align-items: center;
      justify-content: center;
      width: 100%;
      text-align: center;
      transition: all 0.3s;
      margin-top: -20px;
      height: 25%;
      z-index: 3;

  }
  
    .hamburger{
      display: block;
      cursor: pointer;
    }
  
    .hamburger.active .bar:nth-child(1){
      transform: translateY(8px) rotate(45deg);
      cursor: pointer;
    }
  
    .hamburger.active .bar:nth-child(2){
      opacity: 0;
      cursor: pointer;
    }
  
    .hamburger.active .bar:nth-child(3){
      transform: translateY(-8px) rotate(-45deg);
      cursor: pointer;
    }

    header {
      position: fixed;
      top: 0;
    }
}